body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: block;
}

.show {
  opacity: 1;
}

.hide {
  opacity: 0.3;
}

.navbar {
    width: 100%;
    height: 50px;
    background-color: #fff100;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.navbar section {
    margin-left: 10px;
    margin-right: 10px;
}
.navbar a {
    font-weight: bold;
}
.navbar .lines {
    display: flex;
    flex-direction: column;
    cursor: pointer;
}
.navbar .lines .line {
    background-color: #000;
    width: 25px;
    height: 3px;
    margin: 2px;
}
.navbar .lines .midLine {
    background-color: #000;
    width: 22px;
    height: 3px;
    margin: 2px;
}
.navbar .img {
    margin-right: 100px;
}
.navbar .img img {
    width: 100px;
    height: auto;
}
.navbar .links {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.navbar .links .first {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.navbar .links .first p {
    margin-left: 10px;
}
.navbar .links .first a {
    color: #000;
    margin-left: 10px;
    margin-right: 10px;
}
.navbar .links .second {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.navbar .links .second a {
    margin-left: 5px;
    text-decoration: none;
    color: #000;
}
.searchBar {
    background-color: white;
    width: 100%;
    height: 70px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.searchBar .search {
    margin-left: 150px;
}
.searchBar .search input {
    width: 300px;
    height: 35px;
    border-radius: 20px;
    border: #d9d1d1 solid;
    padding-left: 30px;
    padding-right: 30px;
}
.searchBar .search i {
    overflow: hidden;
    margin-right: -25px;
    position: relative;
}
.searchBar .img {
    margin-left: 10px;
    margin-right: 10px;
}
.searchBar .img img {
    width: 80px;
    height: auto;
}
.searchBar .links {
    margin-right: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    width: 500px;
}
.searchBar .links :first-child {
    cursor: pointer;
}
.searchBar .links :first-child span {
    background-color: #fff100;
    color: #000000;
    width: 12px;
    height: 12px;
    font-size: 10px;
    text-align: center;
    border-radius: 50%;
    border: none;
    position: absolute;
    margin-left: -5px;
}
.searchBar .links section a {
    margin-left: 10px;
    text-decoration: none;
    color: #000000;
}
.linksBar {
    background-color: #000000;
    width: 100%;
    height: 50px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.linksBar :first-child {
    margin-left: 20px;
}
.linksBar :last-child {
    margin-right: 35px;
    color: red;
}
.linksBar a {
    text-decoration: none;
    color: white;
    font-size: 15px;
    font-weight: bold;
}
.category {
    margin-left: 140px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
}
.category :last-child {
    text-decoration: none;
}
.category a {
    color: black;
    margin-left: 5px;
    margin-right: 5px;
    margin-top: 10px;
    margin-bottom: 10px;
    font-weight: 600;
}

.product {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.product .images {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: -100px;
}
.product .images .mainImage {
    position: relative;
    text-align: center;
}
.product .images .mainImage img {
    width: 600px;
    border-radius: 10px;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.product .images .mainImage i {
    background-color: white;
    cursor: pointer;
    border: none;
    position: absolute;
    overflow: hidden;
    width: 50px;
    height: 50px;
    top: 10px;
    left: 20px;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.product .images .mainImage i img {
    width: 40px;
    height: 40px;
}
.product .images .moreImages {
    margin-top: 30px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.product .images .moreImages img {
    width: 160px;
    height: auto;
    margin-left: 10px;
    margin-right: 10px;
}
.product .details {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
}
.product .details hr {
    width: 100%;
    height: 2px;
    background-color: lightgrey;
    border: none;
}
.product .details .price {
    margin-bottom: 5px;
}
.product .details .price img {
    width: 50px;
    height: auto;
}
.product .details .price .description {
    font-size: 20px;
    font-weight: 700;
    width: 400px;
}
.product .details .price h3 {
    font-size: 20px;
    font-weight: 700;
    color: lightgrey;
}
.product .details .price .rating {
    font-size: 20px;
    font-weight: 700;
    margin-left: 10px;
    margin-right: 10px;
}
.product .details .price .numberOfRates {
    font-size: 20px;
    color: gray;
    text-align: center;
}
.product .details .price h1 {
    font-size: 30px;
    font-weight: 700;
    color: #542e90;
    display: inline-block;
    margin-right: 10px;
}
.product .details .price .le {
    font-size: 20px;
    color: #542e90;
    margin-right: 20px;
}
.product .details .price .oldPrice {
    color: grey;
    margin-right: 20px;
}
.product .details .price .line {
    background-color: grey;
    width: 55px;
    height: 1px;
    position: absolute;
    margin-top: 73px;
}
.product .details .price .sale {
    display: inline-block;
    background-color: #fff100;
    width: 70px;
    height: 20px;
    text-align: center;
    font-size: 15px;
    font-weight: 500;
}
.product .details .size {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left: -30px;
    margin-bottom: 10px;
}
.product .details .size h1 {
    font-size: 20px;
    font-weight: 700;
}
.product .details .size .sizeCategories {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.product .details .size .sizeCategories span {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    border: 0px solid lightgrey;
    color: #542e90;
    text-align: center;
    font-size: 12px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-left: 0;
    margin-right: 10px;
}
.product .details .size .sizeCategories .activeSize {
    background-color: lightgrey;
}
.product .details .color {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-left: -200px;
    margin-bottom: 10px;
}
.product .details .color h1 {
    font-size: 20px;
    font-weight: 700;
}
.product .details .color .colorCategories {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.product .details .color .colorCategories .activeColor {
    border: 1px solid #542e90;
}
.product .details .color .colorCategories span {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    color: #542e90;
    text-align: center;
    font-size: 12px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-left: 0;
    margin-right: 10px;
}
.product .details .color .colorCategories span img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
}
.product .details .quantity {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    margin-bottom: 10px;
}
.product .details .quantity h1 {
    font-size: 20px;
    font-weight: 700;
}
.product .details .quantity .number {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    text-align: center;
    width: 200px;
    height: 30px;
    background-color: lightgrey;
    padding-right: 5px;
    padding-left: 5px;
    border-radius: 20px;
    margin-bottom: 10px;
}
.product .details .quantity .number button {
    border-radius: 50%;
    border: none;
    background-color: #fff100;
    cursor: pointer;
    font-size: 15px;
}
.product .details .quantity .cartButtons {
    width: 400px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.product .details .quantity .cartButtons button {
    width: 200px;
    height: 40px;
    font-size: 13px;
    font-weight: 800;
    text-align: center;
    border-radius: 25px;
    border: none;
    cursor: pointer;
}
.product .details .quantity .cartButtons .add {
    background-color: #542e90;
    color: white;
    margin-right: 10px;
}
.product .details .quantity .cartButtons .pickup {
    background-color: #fff100;
    color: black;
}
.footer {
    background-color: #413e3e;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding: 40px 150px;
    color: white;
}
.footer .details {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.footer .details .textSection {
    width: 40%;
}
.footer .details .textSection .img {
    margin-bottom: 20px;
}
.footer .details .textSection .img img {
    width: 200px;
    height: auto;
}
.footer .details .firstVertical {
    background-color: #545252;
    width: 1px;
    height: 350px;
}
.footer .details .subscribeSection {
    width: 50%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}
.footer .details .subscribeSection .subscribe {
    width: 100%;
    margin-bottom: 60px;
}
.footer .details .subscribeSection .subscribe .subscribeInput {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.footer .details .subscribeSection .subscribe .subscribeInput input {
    color: #542e90;
    border: none;
    font-size: 16px;
    font-weight: 700;
    outline: none;
    width: 90%;
    height: 50px;
    border-radius: 35px;
    padding: 10px;
}
.footer .details .subscribeSection .subscribe .subscribeInput input ::-webkit-input-placeholder {
    color: #542e90;
}
.footer .details .subscribeSection .subscribe .subscribeInput input ::placeholder {
    color: #542e90;
}
.footer .details .subscribeSection .subscribe .subscribeInput button {
    position: absolute;
    cursor: pointer;
    margin-left: 400px;
    height: 50px;
    width: 120px;
    border-radius: 25px;
    border: none;
    color: black;
    font-weight: bold;
    background-color: #fff100;
    text-align: center;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
}
.footer .details .subscribeSection .links {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    justify-content: space-between;
    margin-top: -30px;
}
.footer .details .subscribeSection .links .leftLinks {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    margin-right: 100px;
}
.footer .details .subscribeSection .links .leftLinks a {
    margin-top: 5px;
    margin-bottom: 5px;
    color: white;
    text-decoration: none;
}
.footer .details .subscribeSection .links .secondVertical {
    background-color: #545252;
    width: 1px;
    height: 190px;
    margin-right: 50px;
}
.footer .details .subscribeSection .links .rightLinks {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
}
.footer .details .subscribeSection .links .rightLinks div {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}
.footer .details .subscribeSection .links .rightLinks div .linkImg {
    width: 30px;
    height: auto;
    margin-right: 10px;
}
.footer hr {
    width: 100%;
    height: 1px;
    background-color: #545252;
    border: none;
}
.footer .rightsSection {
    width: 100%;
    margin-top: 20px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.footer .rightsSection .paying {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.footer .rightsSection .paying img {
    margin-right: 10px;
}
.footer .rightsSection .nasnav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.footer .rightsSection .nasnav img {
    margin-left: 5px;
}
.cart {
    opacity: 1;
    width: 450px;
    height: 100%;
    background-color: white;
    margin-top: -20px;
    right: 0;
    top: 15px;
    position: fixed;
    padding: 30px 20px;
    box-shadow: 5px 5px 10px black;
}
.cart span {
    position: absolute;
    right: 20px;
    cursor: pointer;
}
.cart h1 {
    color: #542e90;
    text-align: center;
}
.cart h2 {
    text-align: center;
}
.cart .buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.cart .buttons button {
    width: 250px;
    height: 30px;
    font-size: 12px;
    border-radius: 25px;
    border: none;
    cursor: pointer;
}
.cart .buttons :first-child {
    margin-right: 5px;
    background-color: #fff100;
    color: black;
}
.cart .buttons :last-child {
    background-color: #542e90;
    color: white;
}
.cartItem .item {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    border: 1px solid lightgrey;
    border-radius: 10px;
    padding: 8px;
}
.cartItem .item .itemImg {
    text-align: center;
    margin-right: 10px;
}
.cartItem .item .itemImg img {
    width: 150px;
    height: auto;
    border-radius: 10px;
}
.cartItem .item .itemDetails {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-evenly;
}
.cartItem .item .itemDetails div {
    width: 250px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.cartItem .item .itemDetails div span {
    font-size: 10px;
    color: #542e90;
    margin-right: 225px;
    margin-top: -35px;
    position: absolute;
}
.cartItem .item .itemDetails div button {
    background-color: #fff100;
    border-radius: 12px;
    border: none;
    color: #3c3c3b;
    font-size: 10px;
    font-weight: 700;
    padding: 5px 18px;
    cursor: pointer;
}
.cartItem .item .itemDetails .description {
    font-size: 10px;
    font-weight: bold;
}
.cartItem .item .itemDetails .quantity {
    font-size: 10px;
}
